import React from "react";
import "./Search.css";

function Bookresults(props) {
  console.log("I'm props.results: " + Object.values(props.results));
  const update = Object.values(props.results);
  return (
    <div className="searchResultsBox" >
      <ul className="resultsBoxes">
        <li> hello </li>
        {update.map(result => (
          <li className="resultsBoxes" key={result.id}>
            Hello {result.title}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Bookresults;
